	module.exports = {"pathContext":{"docHtml":"<h1>github-webhook-handler</h1>\n<p><a href=\"https://nodei.co/npm/github-webhook-handler/\"><img src=\"https://nodei.co/npm/github-webhook-handler.png?downloads=true&#x26;downloadRank=true\" alt=\"NPM\"></a>\n<a href=\"https://nodei.co/npm/github-webhook-handler/\"><img src=\"https://nodei.co/npm-dl/github-webhook-handler.png?months=6&#x26;height=3\" alt=\"NPM\"></a></p>\n<p>GitHub allows you to register <strong><a href=\"https://developer.github.com/webhooks/\">Webhooks</a></strong> for your repositories. Each time an event occurs on your repository, whether it be pushing code, filling issues or creating pull requests, the webhook address you register can be configured to be pinged with details.</p>\n<p>This library is a small handler (or \"middleware\" if you must) for Node.js web servers that handles all the logic of receiving and verifying webhook requests from GitHub.</p>\n<h2>Tips</h2>\n<p>In Github Webhooks settings, Content type must be <code>application/json</code>.</p>\n<p><code>application/x-www-form-urlencoded</code> won't work at present.</p>\n<h2>Example</h2>\n<pre><code class=\"language-js\">var http = require('http')\nvar createHandler = require('github-webhook-handler')\nvar handler = createHandler({ path: '/webhook', secret: 'myhashsecret' })\n\nhttp.createServer(function (req, res) {\n  handler(req, res, function (err) {\n    res.statusCode = 404\n    res.end('no such location')\n  })\n}).listen(7777)\n\nhandler.on('error', function (err) {\n  console.error('Error:', err.message)\n})\n\nhandler.on('push', function (event) {\n  console.log('Received a push event for %s to %s',\n    event.payload.repository.name,\n    event.payload.ref)\n})\n\nhandler.on('issues', function (event) {\n  console.log('Received an issue event for %s action=%s: #%d %s',\n    event.payload.repository.name,\n    event.payload.action,\n    event.payload.issue.number,\n    event.payload.issue.title)\n})\n</code></pre>\n<p>for multiple handlers, please see <a href=\"https://github.com/rvagg/github-webhook-handler/pull/22#issuecomment-274301907\">multiple-handlers-issue</a>.</p>\n<h2>API</h2>\n<p>github-webhook-handler exports a single function, use this function to <em>create</em> a webhook handler by passing in an <em>options</em> object. Your options object should contain:</p>\n<ul>\n<li><code>\"path\"</code>: the complete case sensitive path/route to match when looking at <code>req.url</code> for incoming requests. Any request not matching this path will cause the callback function to the handler to be called (sometimes called the <code>next</code> handler).</li>\n<li><code>\"secret\"</code>: this is a hash key used for creating the SHA-1 HMAC signature of the JSON blob sent by GitHub. You should register the same secret key with GitHub. Any request not delivering a <code>X-Hub-Signature</code> that matches the signature generated using this key against the blob will be rejected and cause an <code>'error'</code> event (also the callback will be called with an <code>Error</code> object).</li>\n<li><code>\"events\"</code>: an optional array of whitelisted event types (see: <em>events.json</em>). If defined, any incoming request whose <code>X-Github-Event</code> can't be found in the whitelist will be rejected. If only a single event type is acceptable, this option can also be a string.</li>\n</ul>\n<p>The resulting <strong>handler</strong> function acts like a common \"middleware\" handler that you can insert into a processing chain. It takes <code>request</code>, <code>response</code>, and <code>callback</code> arguments. The <code>callback</code> is not called if the request is successfully handled, otherwise it is called either with an <code>Error</code> or no arguments.</p>\n<p>The <strong>handler</strong> function is also an <code>EventEmitter</code> that you can register to listen to any of the GitHub event types. Note you can be specific in your GitHub configuration about which events you wish to receive, or you can send them all. Note that the <code>\"error\"</code> event will be liberally used, even if someone tries the end-point and they can't generate a proper signature, so you should at least register a listener for it or it will throw.</p>\n<p>See the <a href=\"https://developer.github.com/webhooks/\">GitHub Webhooks documentation</a> for more details on the events you can receive.</p>\n<p>Included in the distribution is an <em>events.json</em> file which maps the event names to descriptions taken from the API:</p>\n<pre><code class=\"language-js\">var events = require('github-webhook-handler/events')\nObject.keys(events).forEach(function (event) {\n  console.log(event, '=', events[event])\n})\n</code></pre>\n<p>Additionally, there is a special <code>'*'</code> even you can listen to in order to receive <em>everything</em>.</p>\n<h2>License</h2>\n<p><strong>github-webhook-handler</strong> is Copyright (c) 2014 Rod Vagg <a href=\"https://twitter.com/rvagg\">@rvagg</a> and licensed under the MIT License. All rights not explicitly granted in the MIT License are reserved. See the included <a href=\"./LICENSE.html\">LICENSE.md</a> file for more details.</p>","docPath":"argo-ci/node_modules/github-webhook-handler/readme","proj":"argo-ci"}}

